<template>
  <v-autocomplete
    label="Conta"
    no-data-text="Vazio"
    hide-details
    :loading="loading"
    :items="items"
    @change="changeHandler"
  >
    <template v-slot:selection="{ item }">
      <v-chip
        label
      >
        <v-icon
          left
        >
          account_balance
        </v-icon>
        Conta: {{ item.text }}
      </v-chip>
      <v-chip
        label
      >
        <v-icon
          left
        >
          account_balance_wallet
        </v-icon>
        Saldo: {{ item.value.saldoAtual | currency }}
      </v-chip>
    </template>
    <template v-slot:item="{ item }">
      <v-list-tile-content>
        <v-list-tile-title>
          {{ item.text }}
        </v-list-tile-title>
        <v-list-tile-sub-title>
          Saldo: {{ item.value.saldoAtual | currency }}
        </v-list-tile-sub-title>
      </v-list-tile-content>
    </template>
  </v-autocomplete>
</template>

<script>
import API from "@/api/";

export default {
  props: {
    value: {
      type: Object,
      default: () => null,
    },
  },

  data () {
    return {
      minhasContas: [],
      loading: true,
    }
  },

  computed: {
    items () {
      return this.minhasContas
        .filter(it => it.ativo)
        .map(it => {
          return {
            text: it.numero,
            value: it
          }
        })
    },
  },

  mounted () {
    this.load()
  },

  methods: {
    load () {
      this.loading = true
      API.Conta.getMinhasContas((err, contas) => {
        if (!err) {
          this.minhasContas = contas
        }
        this.loading = false
      })
    },

    changeHandler (val) {
      this.$emit('input', val)
    },
  },
}
</script>
